.full-height {
  height: 55vh;
  display: flex;
  flex-direction: column;
}

.hide-scrollbar {
  overflow-y: auto;
  -ms-overflow-style: none;  
  scrollbar-width: none; 
}

.hide-scrollbar::-webkit-scrollbar {
  display: none;
}

.stylized-hr {
  border: 0;
  height: 1px;
  background: linear-gradient(to right, #f1f1f1, #888, #f1f1f1);
  margin: 10px 0;
}

.loading-spinner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 2rem;
}

.spinner {
  border: 8px solid rgba(0, 0, 0, 0.1);
  border-top: 8px solid #3498db;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}